.navbar{
    display: flex;
    padding : 15px;
    align-items: center;
    font-family: Proxima Soft;
    font-size: larger;
    justify-content: space-around;
}

.container{
    display: flex;
}

ul{
    list-style-type : none;
    display : flex;
    gap : 40px;
}

ul li a{
    text-decoration : none;
    
}

.comp1{
    font-size: 1.6rem;
}

ul li a:hover{
    color : #3d3d3d;
}

#comps{
    font-size: 1.9rem;
}

.btn{
    background-color: rgb(17, 17, 17);
    width : 250px;
    border: none;
    border-radius: 250px;
    padding: 15px;
    cursor: pointer;
    font-size: larger;
}

.btn:hover{
    background-color: rgb(36, 35, 35);
}
