* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .container-abtus {
    max-width: 80%;
    margin: 0 auto;
    padding: 20px;
  }
  
  .about-us {
    background-color: #f9f9f9;
    padding: 40px 0;
  }
  
  .about-us h2 {
    font-size: 70px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .about-us p {
    font-size: 35px;
    color: #555;
    line-height: 1.6;
    margin-bottom: 35px;
  }
  
  
  .about-us p:last-child {
    margin-bottom: 0;
  }
  

