.news-body {
    width : 80%;
    margin : 0 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.card-container {
    margin: 10px 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 70px;
}

.container{
    display: flex;
    width: 80%;
    margin : 9px 10%;
    justify-content: space-between;
}

.btn{
    background-color: #3d3d3d;
    color : white;
    padding : 15px;
    border : none;
    border-radius: 5px;
    cursor: pointer;
}
