.card{
    width : 600px;
    height: auto;
    box-shadow: 0 26px 58px 0 rgba(0, 0, 0, .22), 0 5px 14px 0 rgba(0, 0, 0, .18);
    padding : 10px;
    align-content: center;
    /* display: flex;
    flex-direction: column;
    align-items: center; */
    
}


.card img{
    width : 500px;
    height : auto;
}

#btn{
    background-color: rgb(17, 17, 17);
    border: none;
    border-radius: 5px;
    padding: 15px;
    cursor: pointer;
    font-size: larger;
}

#btn:hover{
    background-color: rgb(36, 35, 35);
}

#btn a{
    text-decoration: none;
    color: white;
}